<template>
  <div class="menu">
    <van-search
    v-model="keywords"
      shape="round"
      background="rgb(249,187,50)"
      input-align="center"
      placeholder="请输入搜索关键词"
      show-action
      @search='onSearch'
    />
    <van-notice-bar scrollable left-icon="volume-o" text="亲爱的客人，请您在饮用时注意小心热饮哟💗~" />
    <!-- 左边侧边栏  @search="onSearch"v-model="value"-->
    <div class="menuContent">
      <div class="aside fl">
        <div
          class="item"
          v-for="(item, index) in typeData"
          :key="index"
          :class="{active:item.isActive}"
          @click="toggle(item,index)"
          :ref="'item' + index"
        >{{item.typeDesc}}</div>

        <!-- 移动 top位置 原始位置top=0 关联style-->
        <div class="line" :style="{top: top + 'px'}"></div>
      </div>

      <!-- 右边内容(有左右盒子 便于设置中间间距) content fl claerfix直接使用App.vue的样式 -->
      <div class="content fl claerfix">
        <!-- 轮播图 swipe引入main.js-->
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in bannerData" :key="index">
            <img class="auto-img" :src="item.bannerImg" alt />
          </van-swipe-item>
        </van-swipe>

        <div class="con">
          <div class="productlist clearfix">
            <div
              class="content-item fl"
              :class="[index %2 ==0 ?'left':'right' ]"
              v-for="(item,index) in typeProduct"
              :key="index"
              @click="goDetail(item.pid)"
            >
              <div>
                <img class="auto-img" :src="item.smallImg" alt />
              </div>
              <div class="pro-name one-text">{{item.name}}</div>
              <div class="english one-text">{{item.enname}}</div>
              <div class="pro-price one-text">{{item.price}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/menu.less";
//导入命名空间 使用的是 menuModule
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("menuModule");

export default {
  data() {
    return {
      // banner轮播图数据 type类型数据
      bannerData: [],
      //左侧边栏数据
      typeData: [],
      // 右边top为线条line位置 左边侧边栏高度height
      top: 0,
      //height: 0, 存储在vuex
      //右侧商品类型数据
      typeProduct: [],
      icons: [
        "https://s1.ax1x.com/2020/09/03/wisSZ6.png",
        "https://s1.ax1x.com/2020/09/04/wFq5zd.png",
        "https://s1.ax1x.com/2020/09/04/wFqbeP.png",
        "https://s1.ax1x.com/2020/09/04/wFqXFS.png",
      ],
      keywords:''
    };
  },

  //调用 methods 里面的方法
  created() {
    //调用 获取banner数据，type数据
    this.getBanner();
    this.getType();
    //关联 对象
    this.getTypeProduct({ type: "isHot" });
    
    this.moveLine();
  },

  computed: {
    //使用vuex的state
    ...mapState(["bannerState", "asideState", "productsState", "height"]),
  },

  methods: {
    ...mapMutations(["changeData", "changeProducts"]), //mutations提交commit改变数据
    //初始化移动条line。 避免vuex缓存数据返回 未切换 .需要等待$nextTick
    moveLine() {
      
      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.top = i * this.height;
          return;
        }
      }
    },
    //获取banner数据
    getBanner() {
      //请求vuex的state中的bannerState轮播图数据。如果存在缓存数据就不用再重新请求vuex了
      if (this.bannerState.length > 0) {
        this.bannerData = this.bannerState;
        
        return;
      }

      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0, //延迟自动关闭加载提示，当为0不会自动关闭需要手动，在then解决关闭
      });

      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
          if (result.data.code == 300) {
            //给bannerData数据赋值 之后在视图遍历字段bannerImg
            this.bannerData = result.data.result;

            //缓存banner数据在vuex中 menuModule下的方法changeData
            /* this.$store.commit('menuModule/changeData',{
              key:'bannerState',
              value:'result.data.result'
            })*/
            this.changeData({ key: "bannerState", value: result.data.result });
          }
        })
        .catch((err) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
        });
    },
    //商品类型. 参数有key 和type
    getType() {
      if (this.asideState.length > 0) {
        this.typeData = this.asideState;
        
        return;
      }
      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0, //延迟自动关闭加载提示，当为0不会自动关闭需要手动，在then解决关闭
      });

      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
          if (result.data.code == 400) {
            result.data.result.forEach((v) => {
              v.isActive = false; //第一个为true其他为false
            });
            //先push再给type数据赋值 之后在视图v-for遍历 unshift头push尾
            result.data.result.unshift({
              type: "isHot",
              typeDesc: "最新推荐",
              isActive: true,
              // icons: this.icons,
            });
            this.typeData = result.data.result;
            

            //1.等数据渲染完成执行Dom操作 左侧线条top高度 使用vuex存储height
            this.$nextTick(() => {
              //$refs 获取第一个item+i[0]的高度 44
              let firstItemHeight = this.$refs.item0[0].clientHeight;
              //
              //this.height = firstItemHeight;
              this.changeData({ key: "height", value: firstItemHeight });
            });
            //缓存左侧边栏asideState的数据
            this.changeData({ key: "asideState", value: result.data.result });
          }
        })
        .catch((err) => {
          //成功加载就清除 提示
          this.$toast.clear();
          
        });
    },
    //切换左侧菜单
    toggle(item, index) {
      if (item.isActive) {
        return;
      }
      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.typeData[i].isActive = false;
        }
      }
      item.isActive = true;
      //let firsth = this.$refs;
      //2.等height高度被赋值 后 根据index切换top
      this.top = this.height * index;

      this.getTypeProduct(item);
    },
    //类型商品数据. 切换标签 数据也跳转
    getTypeProduct(item) {
      //缓存vuex数据
      //
      if (this.productsState[item.type]) {
        this.typeProduct = this.productsState[item.type];
        
        return;
      }

      this.typeProduct = []; //可以让图片加载更快
      //加载轻提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0, //延迟自动关闭加载提示，当为0不会自动关闭需要手动，在then解决关闭
      });

      let params = {
        appkey: this.appkey,
        key: "type",
        value: item.type,
      };
      if (item.type == "isHot") {
        //类型：最新推荐. 然后在getType调用类型对象
        params.key = "isHot";
        params.value = 1;
      }
      

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params, //传递params的appkey。key。value
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 500) {
            //在视图 遍历v-for
            this.typeProduct = result.data.result;

            //vuex的mutations缓存商品数据 type为参数params的type
            this.changeProducts({ type: item.type, value: result.data.result });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //跳转商品详情页
    goDetail(pid) {
      //查询参数query:{pid}
      this.$router.push({ name: "Details", query: { pid } });
    },
     // 跳转搜索
    onSearch(keywords) {
      if(keywords==''){
        return;
      }
      keywords= this.keywords;
      this.$router.push({ name: "Search" ,query:{keywords}});
    },
  },
};
</script>

<style lang="less" scoped>
</style>